@import 'assets/scss/utils/mixins';

@include media-breakpoint-down(lg) {
	.nav-items {
		width: 100%;
		justify-content: center;
	}
}
a.tab,
button.tab {
	min-width: 59px;
	padding: 0;
	border: none;
	position: relative;
	top: 45px;
	right: 25px;
	@media (min-width: 768px) {
		right: 15px;
	}
	@media (min-width: 1400px) {
		right: 85px;
	}
	div.link-content {
		display: flex !important;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		row-gap: 0.25rem;
		column-gap: 1rem;
		background: var(--basic-1);

		mat-icon {
			color: var(--general-5);
		}

		span {
			display: block;
			color: var(--general-3);
		}
	}

	&.place-ad div.link-content {
		mat-icon {
			color: var(--general-5) !important;
		}
	}

	&.active div.link-content {
		mat-icon,
		span {
			color: var(--prim-2);
		}
	}
}
.mob-header {
	margin-left: 63px !important;
}
.btn-border {
	border: 0px solid var(--prim-2) !important;
	color: var(--prim-2) !important;
}
.header-panel {
	// margin-top: -33px !important;
	position: relative;
	top: -33px;
}
.header-style {
	bottom: 35px;
	position: relative;
}
